import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { Keyboard } from '@ionic-native/keyboard';

import {MobileAppSystemP2l} from '../../../providers/mobile.app.system.p2l'
import {User} from '../../../providers/user'
import {AlertService} from '../../../providers/alert.service'
import {ModalService} from '../../../providers/modal.service'
import { CustomKeyBoard } from '../../../components/customKeyBoard/custom-keyboard';

/**
 * Generated class for the ScanProductPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

export class ProductInfo{
  countProductScanned:number = 0;
  countTotalProducts:number = 0;

  jobID:number=0;  
  binLocation:string = '';
  description:string= '';
  img_url:string= '';
  jobComplete:number=0;
  pickQty:number=0;
  pickUnit:string= '';
  stockCode:string= '';
}


@IonicPage()
@Component({
  selector: 'page-p2l-scan-product',
  templateUrl: 'p2l-scan-product.html'
})
export class P2lScanProductPage {
  @ViewChild('barCodeInputBox') barCodeInput;
  @ViewChild('confirmedInputBox') confirmInput;

  private productBarCode:string = '';
  private titleDefault :string; 
  private title :string;
  private bLastPressReturnKey:boolean = true;
  private bBinLocationScaning:boolean = false;
  private productInfo: ProductInfo;
  private confirmedPick :string;
  private confirmedPickFocused :boolean = false;

  private pickUnitSensitive: boolean = false;



  constructor(public navCtrl: NavController, 
              public navParams: NavParams, 
              private keyboard:Keyboard,
              public mobileAppSystem:MobileAppSystemP2l,
              private modalService:ModalService,
              public translateService:TranslateService, public user:User, private alertService:AlertService) {
    this.title = '';
    this.titleDefault = '';
    this.productInfo = new  ProductInfo();
    this.confirmedPick = '';

    let svc = this;
    translateService.get('SCANPRODUCT_TITLE').subscribe(
      value => {
        svc.titleDefault = value;
      }
    ); 

    //init display
    this.getNextProduct();

    CustomKeyBoard.hide();
    this.timerTick();
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad ScanProductPage');
  }


private getNextProduct()
{
  let svc = this;
  this.mobileAppSystem.getNextProduct(this.user.p2lJobID,function(res:any){
    if(res==null)return;
    if(res.isError)
    {
      return;
    }
    else
    {
      svc.productInfo = res.result.productList[0];
      svc.confirmedPick = String(svc.productInfo.pickQty);
      svc.title = svc.titleDefault + " : " + svc.productInfo.countProductScanned + " of " + svc.productInfo.countTotalProducts + " done";    
    }
  });
}


private destroyP2LLabels(p2lLabel:string)
{
  let svc = this;

  svc.mobileAppSystem.destroyP2LLabels(svc.user.p2lJobID, svc.user.sessionInfo.userWarehouse, svc.productInfo.stockCode,
    svc.productInfo.pickQty, Number(this.confirmedPick), p2lLabel, function(res:any)
    {
      if(res==null)return;

      if(res.result.statusCode!= 200)
      {   
        svc.alertService.doAlert('Error', res.result.statusMsg, 'OK');
          return;
      }
      else
      {
        //job completed?
        svc.alertService.doAlert('Product Complete', '', 'OK').then(function(res:any)
        {
           svc.getNextProduct(); 
        });
      }
  });  
}



private callConfirmQty(p2lLabel:string)
{
  let svc = this;
  if(this.confirmedPick =='')
    return;

  svc.mobileAppSystem.confirmPickQty(svc.user.p2lJobID, svc.user.sessionInfo.userWarehouse, svc.productInfo.stockCode,
    svc.productInfo.pickQty, Number(this.confirmedPick), function(res:any)
    {
      if(res==null)return;
      if(res.result.statusCode!= 200)
      {   
          svc.alertService.doAlert('Error', res.result.statusMsg, 'OK');
          return;
      }
      else
      {
        svc.destroyP2LLabels(p2lLabel);
      }
  });  

}

private inputP2lLabel()
{
  let svc = this;
  this.bBinLocationScaning = true;  
  this.modalService.doPrompt('Confirm P2L Label stuck onto Carton!', 'Scan P2L Label', 'Go', 'Cancel','', 'Barcode Here').then(function(p2lLabel)
  {
      svc.bBinLocationScaning = false;
      if(p2lLabel == '')
          return;

      svc.mobileAppSystem.validate1stP2LLabel(svc.user.p2lJobID, svc.user.sessionInfo.userWarehouse, svc.productInfo.stockCode,
        p2lLabel, function(res:any)
        {
          if(res==null)return;
          if(res.result.statusCode != 200)
          {   
              svc.alertService.doAlert('Error', res.result.statusMsg, 'OK');
              return;
          }
          else
          {            
              svc.callConfirmQty(p2lLabel);
          }
      });                  
  });
}

private inputBinLocationCode()
{
  let svc = this;
  this.bBinLocationScaning = true;
  this.modalService.doPrompt('Product Barcode Not Found!', 'Scan or Enter Bin Location', 'Go', 'Cancel','', 'BinLocation').then(function(binLocationCode)
  {
      svc.bBinLocationScaning = false;
      if(binLocationCode == '')
          return;
      svc.mobileAppSystem.validateProductBinBarcode(svc.user.p2lJobID, svc.user.sessionInfo.userWarehouse, svc.productInfo.stockCode,
        binLocationCode, function(res:any)
        {
          if(res==null)return;
          if(res.result.statusCode != 200)
          {   
              svc.alertService.doAlert('Error', res.result.statusMsg, 'OK');
              return;
          }
          else
          {
              svc.inputP2lLabel();
          }
      });                  
  });

}


onChangedProductBarCode(val:any)
{
  if(this.pickUnitSensitive==true)
    return;
  if(val =='')return;
  this.productBarCode =  val;
  let svc = this;    

  this.mobileAppSystem.validateProductBarcode(this.user.p2lJobID, this.user.sessionInfo.userWarehouse, this.productInfo.stockCode, this.productBarCode, function(res:any){
    svc.productBarCode = '';
    if(res==null) return;

    if(res.result.statusCode != 200)
    {   
        svc.alertService.doAlert('Error', res.result.statusMsg, 'OK' ).then(function(yes){
            svc.inputBinLocationCode();
        });
    }
    else
    {
        svc.inputP2lLabel();
    }
  });

}

  timerTick()
  {
    let svc = this;
    setTimeout(() => {

      if(this.navCtrl.getActive().component.name !="P2lScanProductPage"  || this.mobileAppSystem.isBusy()==true)
      {
        this.timerTick();
        return;
      }

      let visibleKeypad = CustomKeyBoard.isVisible();            

      if(svc.bBinLocationScaning ==false)
      {
        if(svc.keyboard.close != null)
           svc.keyboard.close();

         if(svc.confirmedPickFocused ==false)
         {
           if(visibleKeypad==false )
           {
             if(svc.barCodeInput._isFocus ==false)
             {
               svc.barCodeInput._readonly = true;
               svc.barCodeInput.setFocus();
               svc.barCodeInput._readonly = false;
             }               
           }
         }
      }        
      svc.timerTick();

    },100); //a least 150ms.
  }


  onShowKeyPad()
  {
    let svc = this;
    if(CustomKeyBoard.isVisible())
       CustomKeyBoard.hide();
    else
    {
      CustomKeyBoard.show();    
      CustomKeyBoard.setTarget(this.barCodeInput, function(val:string){
        svc.onChangedProductBarCode(val);
      });
    }
  }

  hideKeyboard()
  {    
    CustomKeyBoard.hide();
  }

  onConfirmQtyFocused()
  {
    let svc = this;
    this.confirmedPickFocused = true;    
    CustomKeyBoard.show();
    CustomKeyBoard.setTarget(this.confirmInput, function(val:string){
      svc.confirmedPick = val;
      svc.confirmedPickFocused = false;
    });
  }  

  onClickBarcode()
  {
    if(this.confirmedPickFocused ==true)
    {
      CustomKeyBoard.hide();
      this.confirmedPickFocused = false;
    }
  }
    
}



